.App {
  text-align: center;
}
.App > header {
  background-color: #2196f3 !important;
}
.App > header > div {
  height: 56px !important;
}
.App > header h6 {
  line-height: unset !important;
}
.App-page {
  position: fixed;
  top: 56px;
  left: 0;
  bottom: 0;
  right: 0;
  background: #fefefe;
}
.App-page-favorite-null {
  padding: 20px 0;
  color: #444;
}
.App-page-favorite-list {
  padding-top: 0 !important;
}
.App-news-page {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  overflow-y: auto;
  text-align: left;
}
.App-news-page-about {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 20px;
  flex-direction: column;
}
.App-news-page-about > a {
  margin-top: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 16px;
  color: #666;
}
.App-news-page-about > a img {
  width: 25px;
  height: 25px;
  padding-right: 3px;
}
.App-news-page-about > p {
  margin: 15px 0;
  font-style: italic;
}
.App-news-page-title {
  font-weight: bold;
  line-height: 1.2;
  font-size: 18px;
  padding: 10px 0;
}
.App-news-page-date {
  color: #aaa;
  padding-top: 15px;
  padding-bottom: 10px;
  display: flex;
  font-size: 12px;
  justify-content: space-between;
}
.App-news-page-content {
  padding: 10px 0;
  color: #333;
  text-indent: 2em;
  line-height: 1.8em;
  font-size: 16px;
}
.App-news-page-origin {
  padding-bottom: 10px;
  text-align: right;
}
.App-news-page-origin a {
  color: #00e;
  font-size: 16px;
}
.App-news-page-read {
  position: fixed !important;
  bottom: 15px !important;
  right: 15px !important;
  z-index: 1000;
}
.App-header-search {
  background: rgba(255, 255, 255, 0.15);
  border-radius: 5px;
  margin-left: 10px;
  color: inherit;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}
.App-header-search > div {
  display: inline-block !important;
}
.App-header-search > div:first-child {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  padding-left: 10px;
}
.App-header-search > div:last-child {
  color: inherit;
  padding: 0 5px;
  font-size: 14px;
}
.bar-msg > div {
  display: flex;
  align-items: center;
}
.bar-msg > div svg {
  font-size: 20px;
  margin-right: 8px;
}
.bar-right-ico svg {
  font-size: 20px;
}
.bar-success > div {
  background-color: #43a047;
}
.bar-info > div {
  background-color: #1976d2;
}
.none {
  display: none !important;
}
